import { Component,ViewChild } from '@angular/core';
import { Nav, Platform } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import {ListRequisitionsPage}from '../pages/list-requisitions/list-requisitions';
import { BuildingsPage} from '../pages/buildings/buildings';
import { HomePage } from '../pages/home/home';
@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  @ViewChild(Nav) nav: Nav;
  rootPage:any = HomePage;
  //Variables nombre:tipo
  text: string="";  
  pages: Array<{title: string, component:any}>;
  constructor(platform: Platform, statusBar: StatusBar, splashScreen: SplashScreen) {
    
    platform.ready().then(() => {
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      statusBar.styleDefault();
      splashScreen.hide();
    });

    this.pages = [
      { title: 'Home', component: HomePage },
      { title: 'Mis Solicitudes', component: ListRequisitionsPage },
      { title: 'Ubicaciones', component: BuildingsPage }
    ];

  }
  openPage(page){
    this.nav.setRoot(page.component);
  }
  rightMenuClick(text){
    this.text=text;
  }
  
}




